<template>
  <div id="app">
  
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" :model="conditions" @submit.native.prevent="load">

          <el-form-item>
            <el-input v-model="conditions.name" placeholder=""></el-input>
          </el-form-item>
        <el-form-item>
            <el-select v-model="conditions.typeid" placeholder="" clearable>
              <el-option v-for="item in types" :key="item.name" :label="item.name" :value="item.Id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" v-on:click="load" icon="search"></el-button>
            <el-button type="primary" v-on:click="showEditForm(emptyruleForm)" icon="plus"></el-button>
            <el-button type="primary" v-on:click="showEditType(emptyTypeForm)" icon="plus"></el-button>
            <el-button type="primary" v-on:click="listTypeLoad" icon="information"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

        <!--  -->
      <el-table :data="data" highlight-current-row v-loading="isLoading" id="table">

        <el-table-column type="name" prop="type_name"  label=""></el-table-column>

        <el-table-column prop="title" label=""></el-table-column>
        <el-table-column  label="">
        <template scope="scope">
            <img v-if="scope.row.image" :src="scope.row.image" class="avatar">
          </template>
        </el-table-column>
        <el-table-column prop="author" label=""></el-table-column>
        <el-table-column prop="cTime" label="" :formatter="AddTimeFormatter"></el-table-column>

        <el-table-column label="" min-width="120">
          <template scope="scope">
            <el-button type="text" @click="showEditForm(scope.row)"></el-button>
            <el-button type="text" @click="deleteForm(scope.row)"></el-button>
           <a target="_blank" v-bind:href="scope.row.url"></a>
          </template>
        </el-table-column>

      </el-table>

      <!---->
      <el-col :span="24" class="toolbar">
        
        <span>{{ total }}</span>  
        <el-select v-model="size" size="small" style="width: 70px;" @change="load">
          <el-option v-for="item in sizes" :value="item" :label="item" :key="item"></el-option>
        </el-select>
        
        <el-pagination layout="prev, pager, next" @current-change="handlePageChange" :page-size="size" :total="total" style="float:right;">
        </el-pagination>
      </el-col>

      <el-dialog size='full' :title="ruleForm.Id > 0 ? '' : ''" :visible="editFormVisible" :before-close="() => editFormVisible = false">

          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" >
                <h2></h2>
                <el-form-item label="" prop="name" required>
                      <el-input v-model="ruleForm.name"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="title" required>
                      <el-input v-model="ruleForm.title"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="author" required>
                      <el-input v-model="ruleForm.author"></el-input>
                  </el-form-item>
                  <el-form-item>
                    <el-select v-model="ruleForm.type_id" placeholder="" clearable>
                      <el-option v-for="item in types" :key="item.name" :label="item.name" :value="item.Id"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="" required>
                      <el-upload class="upload-demo" drag :action="fileUploadApi" :on-success="uploadSuccess" :auto-upload="true" accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet">
                          <i class="el-icon-upload"></i>
                          <div class="el-upload__text">
                          <em></em>
                          </div>
                          <div style='display:none;' class="el-upload__tip" slot="tip">png/jpg</div>
                      </el-upload>
                       <img width="20%" :src="ruleForm.image" alt="">
                  </el-form-item>
                  
                  <el-form-item label="" >
                      <el-card>
                              
                              <editor ref="myTextEditor"
                                      :fileName="'myFile'"
                                      :name="'myFile1'"
                                      :canCrop="canCrop"
                                      :uploadUrl="uploadUrl"
                                      @uploadSuccess="onUploadSuccess"
                                      v-model="ruleForm.content"></editor>
                            <div v-html="ruleForm.content"></div>
                      </el-card>
                  </el-form-item>
                
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="editFormVisible = false"></el-button>
                <el-button type="primary" @click="submitForm('ruleForm')" :loading="editFormLoading"></el-button>
              </div>
      </el-dialog>
      <el-dialog size='full'  :visible="listTypeVisible" :before-close="() => listTypeVisible = false">
        <el-table :data="types" highlight-current-row  id="tableType">

              <el-table-column type="name" prop="name"  label=""></el-table-column>
              <el-table-column label="" min-width="120">
                <template scope="scope">
                  <el-button type="text" @click="showEditType(scope.row)"></el-button>
                  <el-button type="text" @click="deleteType(scope.row)"></el-button>
                </template>
              </el-table-column>

          </el-table>
          <div slot="footer" class="dialog-footer">
            <el-button  type="primary"  @click="listTypeVisible = false"></el-button>
          </div>
      </el-dialog>

      <el-dialog  :visible="editTypeVisible" :title="typeForm.Id > 0 ? '' : ''"> 
         <el-form :model="typeForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" >
                
                  <el-form-item label="" prop="name" required>
                      <el-input v-model="typeForm.name"></el-input>
                  </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="editTypeVisible = false"></el-button>
            <el-button type="primary" @click="submitFormType('typeForm')" :loading="editFormTypeLoading"></el-button>
          </div>
      </el-dialog>

    </div>
</template>

<script>
import editor from './components/Quilleditor.vue'
import server from './components/server'
import moment from 'moment'
import clone from 'clone'
export default {
  name: 'app',
  data(){
    return {
     
      canCrop:true,
      /*{url:''}*/
      uploadUrl:server.base+'/file/uploadImg?path=info',//
      fileUploadApi:server.base+'/file/uploadImg?path=info',//

      data: [],

      // 
      apiUrl: '/acticle/GetList',
      table: 'Posts',
      order: 'Id',
      desc: true,
      conditions: {
        name: '',
        typeid: '',
      },
      types:[],//

        // 
      total: 0,
      sizes: [10, 20, 50, 100],
      size: 10,
      page: 1,
      // 
      editFormVisible: false,
      editFormLoading: false,
      editTypeVisible:false,
      editFormTypeLoading:false,
      listTypeVisible:false,
      ruleForm:{
            id:'',
            author:'',
            name:'',
            title:'',
            cover:'',
            image:'',
            content:'',
            type_id:'',
            dialogVisible:false,
        },
        emptyruleForm:{
            id:'',
            author:'',
            name:'',
            title:'',
            cover:'',
            image:'',
            content:'',
            type_id:'',
        },
        emptyTypeForm:{
            id:'',
            name:'',
        },
        rules: {
            name: [
                { required: true, message: '', trigger: 'blur' },
                { min: 2, max: 15, message: ' 2  15 ', trigger: 'blur' }
            ],
            title: [
                {  message: '', trigger: 'blur' },
                { min: 0, max: 25, message: ' 0  25 ', trigger: 'blur' }
            ],
            author: [
                {  message: '', trigger: 'blur' },
                { min: 0, max: 25, message: ' 0  25 ', trigger: 'blur' }
            ],
        },
        typeForm:{
          name:'',
        }
    }
  },
  methods:{
    load: function () {
        server.post('/acticle/Getypes', {op:'gettypes'}, this).then(res => {
          var tmp=res;
          let { IsSuccess, Data } = res;
          if(IsSuccess){
            this.types = Data;
          }
        });
        let o = {
          page: this.page,
          size: this.size,
          table: this.table,
          conditions: this.conditions,
        };
        server.post(this.apiUrl, {model: o}).then(res => {
        let { IsSuccess, Data,TotalCount } = res;
        console.info(Data);
        console.info(IsSuccess);
          if (IsSuccess) {
            this.total = TotalCount;
            this.data = Data;
          }
        });
    },
    listTypeLoad:function(){
       this.listTypeVisible=true;
       console.log(this.types);
       console.log(11);
    },
    AddTimeFormatter: function (row) { return moment(row.cTime).format('YYYY-MM-DD HH:mm:ss'); },
    showEditForm: function (model) {
      if(model){
        if(model.image){
          model.dialogVisible=true;
        }
        this.ruleForm = clone(model);
      }
      // let selected = [];
      // this.findParents(this.departs, model.ParentId, selected);
      // this.editFormModel.ParentId = selected;

      this.editFormVisible = true;
    },
    deleteForm:function(model){
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
           server.post('/acticle/delete', { model: model }, this)
            .then(res => {
              let { IsSuccess, Data,Msg } = res;if (IsSuccess) {
                this.editFormVisible = false;
                this.load();
                this.$message({ type: 'success', message: Msg });
              }  else {
                this.$message({ type: 'error', message: Msg });
              }
            });
        });
      
    },
    showEditType: function (model) {
      if(model){
        this.typeForm = clone(model);
      }
      this.editTypeVisible = true;
    },
    deleteType:function(model){
      this.$confirm(', ?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              server.post('/acticle/deleteType', { model: model }, this)
                .then(res => {
                  let { IsSuccess, Data,Msg } = res;if (IsSuccess) {
                    this.editFormVisible = false;
                    this.load();
                    this.$message({ type: 'success', message: Msg });
                  }  else {
                    this.$message({ type: 'error', message: Msg });
                  }
                });
            });
      },
    handlePageChange: function (page) {
      this.page = page;
      this.load();
    },

    onUploadSuccess(res){//
      if(res.IsSuccess){
            this.editor.focus();
            this.editor.insertEmbed(this.editor.getSelection().index, 'image', res.Msg);
      }
    },
    uploadSuccess(res){//
        if(res.IsSuccess){
          this.ruleForm.image=res.Msg;
          this.ruleForm.dialogVisible=true;
      }
    },
     submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          let model = clone(this.ruleForm);
          
          this.editFormLoading = true;
          server.post('/acticle/edit', { model: model }, this)
            .then(res => {
            
              let { IsSuccess, Data,Msg } = res;
              let successMsg = model.Id > 0 ? '(:=' : '(:=';
              let failureMsg = model.Id > 0 ? '(:=' : '(:=';
              this.editFormLoading = false;
              if (IsSuccess) {
                this.editFormVisible = false;
                this.load();
                this.$message({ type: 'success', message: Msg });
              }  else {
                this.$message({ type: 'error', message: Msg });
              }
            });
        });
    },
    submitFormType(formName){
        this.editFormTypeLoading = true;
         let model = clone(this.typeForm);
        server.post('/acticle/saveType', { model: model }, this)
            .then(res => {
              this.editFormTypeLoading = false;
            
              let { IsSuccess, Data,Msg } = res;
              let successMsg = model.Id > 0 ? '(:=' : '(:=';
              let failureMsg = model.Id > 0 ? '(:=' : '(:=';
              if (IsSuccess) {
                this.editTypeVisible = false;
                this.load();
                this.$message({ type: 'success', message: Msg });
              }  else {
                this.$message({ type: 'error', message: Msg });
              }
            });
    }
  },
  components: {
    editor
  },
   mounted() {
    
    this.load();
  }
}
</script>

<style>
</style>



